.footer {
  padding-right: 0px;
  padding-left: 0px;
  font-size: 8pt;
  padding-bottom: 0px;
  margin: 0px auto;
  width: 100%;
  padding-top: 5px;
  padding-bottom: 68px;

  height: 70px;
  background-color: #a6aaab;
  border-top-style: solid;
  border-top-width: 3px;
  border-top-color: var(--sec-color);
  .fWrapper {
    color: #fff;
    padding-right: 10px;
    padding-left: 10px;
    font-size: 8pt;
    padding-bottom: 10px;
    margin: 0px auto;
    width: 791px;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    .right {
        color: white;
        display: flex;
        flex-direction: column;
        gap: 30px;
      img {
        width: 25px;
        height: 25px;
      }
      .top {
        color: white;
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 14px;
        .info {
            color: black;
            display: flex;
            gap: 25px;
          span {
            font-size: 13px;
          }
        }
      }
      .bottom {
        color: white;
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 14px;
        img {
          color: white;
        }
        span {
          color: white;
        }
      }
    }
  }
  .left {
    display: flex;
    gap: 5px;
    font-size: 13px;
    span {
      color: white;
      cursor: pointer;
    }
  }
}
