.singleart {
  flex: 2;
  padding: 10px;
  .title {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    gap: 5px;
    justify-content: flex-end;
    font-family: arial;
    font-size: 1.7647em;
    font-weight: bold;
    color: var(--main-color);
    span {
      font-size: 18px;
      background: black;
      color: white;
      border-radius: 50%;
      width: 19px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 19px;
      font-weight: 900;
    }
  }
  .writer {
    color: black;
    margin: 10px 0;
    font-size: 17px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 10px;
    span {
      color: gray;
    }
  }
  .category {
    height: 35px;
    padding: 0 15px;
    font-size: 18px;
    background-color: #eee;
    font-weight: bold;
    color: var(--sec-color);
    display: flex;
    align-items: center;
    margin: 5px 0;
  }
  .time {
    font-size: Medium;
    font-weight: bold;
    margin: 5px 0 25px;
  }
  & > img {

      height: 200px;
      width: 240px;
      border-width: 0px;
      border: 1px solid #000000;
    border-width: 0px;
    margin: auto;
    display: block;
  }
  .desc {
    font-size: 16px;
    line-height: 2;
    text-align: justify;
    margin-top: 20px;
  }
  .shareContainer {
    display: flex;
    gap: 12px;
    margin: 50px 0;
  }

  .yvideo {
    margin-top: 30px;
  }
}
