.ContactUs {
  color: black;
  .title {
    height: 35px;
    padding: 0 15px;
    font-size: 19px;
    background-color: #eee;
    font-weight: bold;
    color: var(--sec-color);
    display: flex;
    align-items: center;
  }
  .subtitle {
    font-family: "arial";
    font-size: 1.7647em;
    font-weight: bold;
    color: var(--main-color);
    margin: 10px 0;
  }
  .aboutcontent {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    .item {
      font-weight: 600;
      font-size: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;
      span:first-of-type {
        display: flex;
        align-items: center;
        gap: 3px;
        span {
          margin-top: -5px;
        }
      }
      .red {
        color: #ff0000;
      }
    }
  }
}
