.homeMainContent {
  display: flex;
  gap: 10px;
  padding: 10px;
  background-color: white;
  margin: 0 10px;
  height: fit-content;

  .right {
    flex: 2 1;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 5px;
    align-content: flex-start;

    .section {
      width: calc(50% - 2.5px);
      h2 {
        background: var(--sec-color);
        color: #fff;
        border-bottom: 2px solid #fff;
        padding: 3px 10px 3px 0;
        font-size: 18px;
        position: relative;
        img {
          position: absolute;
          top: 0;
          bottom: 0;
          margin: auto;
          left: 20px;
          cursor: pointer;
        }
      }
      .bottomContent {
        padding: 5px;
        background-color: #eee;
        margin-top: 3px;
        .bestNew {
          border-bottom: 1px solid #c7c9ca;
          padding-bottom: 5px;
          margin-bottom: 10px;
          img {
            width: 100%;
          }
          .desc {
            font-family: sans-serif, Arial;
            color: #444;
            margin: 0;
            padding: 0 5px 0px;
            font-size: 15px;
            overflow: hidden;
            height: 46px;
            font-weight: bold;
            line-height: 22px;
            margin-top: 5px;
          }
        }
        .bottomNews {
          color: inherit;
          .one {
            display: flex;
            flex-direction: row-reverse;
            margin-bottom: 5px;
            border-bottom: 1px solid #c7c9ca;
            padding-bottom: 5px;
            img {
              width: 140px;
              color: inherit;
              height: 85px;
            }
            .desc {
              color: inherit;
            }
          }
        }
      }
    }
  }
  .left {
    color: inherit;
    flex: 1;

    background: #eee none repeat scroll 0 0;
    border: 1px solid #ccc;
    padding: 0px 9px 9px 9px;
    h2 {
        background: var(--sec-color);
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        justify-content: flex-end;
        color: white;
        gap: 5px;
        margin-top: 9px;
        margin-bottom: 9px;
      img {
        width: 30px;
        height: 30px;
      }
    }
    .videos {
        padding-right: 2px;
        border: 1px solid #ccc;
        background: #eee none repeat scroll 0 0;
        background-repeat: repeat;
        // padding-top: 10px;
        padding-bottom: 0px;
        .once {
          color: black;
          height: 240px;
          display: flex;
          flex-direction: column;
          gap: 5px;
          img {
            max-width: 100%;
            color: black;
            height: 160px;
          }
          .desc {
            font-size: 17px;
          }
        }
    }
  }
}
