@font-face {
    font-family: "arial";
    src: url("./fonts/ArialTh.ttf");
  }
:root {
  --main-color: #c16e67;
  --sec-color: #e5aca4;

  // --main-color: #364649;
  // --sec-color: #708f96;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
* {
  font-family: "Open Sans", sans-serif;
}
