.app {
  background-color: #eee;
position: relative;
  .middleContent {
    display: flex;
    background: white;
    margin: 10px;
    min-height: calc(100vh - 361px);
    .sidebar {
      flex: 1;
    }
  }
}
a{
  text-decoration: none;
  color: inherit;
}

