.myNavbar {
  background-color: white;
  .wrapper {
    background-color: var(--main-color);
    height: 25px;
    .top {
      color: white;
      height: 22px;
      margin: auto;
      width: 970px;
      font-size: 13px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .date {
        font-weight: bold;
        font-size: 14px;
        span {
          color: inherit;
    margin-right: 5px;
        }
      }
      .links {
        color: inherit;
        display: flex;
        gap: 10px;

        li {
          padding-right: 9px;
          padding-left: 9px;
          padding-bottom: 0px;
          color: #fff;
          padding-top: 0px;
          border-left: 1px solid white;
          font-size: 10px;
          cursor: pointer;
          font-weight: bold;
          font-family: sans-serif, Arial;
          &:hover {
            color: #ccc;
          }
        }
      }
    }
  }
  .middle {
    margin: 0px auto;
    width: 991px;
    position: relative;
    height: 173px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 5px 0;
    .logo {
      color: black;
      // width: 600px;
      // height: 140px;
      img {
      
    height: 176px;
    width: 360px;
    position: relative;

        position: relative;
      }
      span {
        position: relative;
        bottom: 23px;
        font-weight: 900;
        font-size: 19px;
        color: #010101;
    
      }
    }
    .social {
      display: flex;
      gap: 15px;
      margin-top: 35px;
      img {
        cursor: pointer;
        width: 35px;
      }
    }
  }
  .googleAd {
    height: 280px;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .bwrapper {
      height: 30px;
      background-color: var(--main-color);
    .bottom {
        width: 970px;
        height: 30px;
        margin: auto;
        position: relative;
        display: flex;
        align-items: center;
        color: white;
        gap: 4px;
      li {
        padding: 8px 2px;
        font-size: 15px;
        color: #FFFFFF;
        line-height: 20px;
        font-weight: bold;
        font-family: "arial";

        img {
          height: 22px;
          margin-top: 4px;
          border-width: 0px;
          padding-left: 6px;
        }
      }
    }
  }
}
