.slider {
  display: flex;
  gap: 20px;
  border: 5px;
  margin: 10px;
  background: white;
  height: 460px;

}

.current-image {
  margin-bottom: 10px;
  flex: 2;
  margin-bottom: 10px;
  flex: 2 1;
  margin: 5px 0;
}

.current-image img {
  max-width: 100%;
  max-height: 100%;
}

.slides {
  display: flex;
  flex: 1.5;
  align-content: flex-start;

  flex-wrap: wrap;
}

.slide {
  flex: 0 0 auto;
  margin-right: 10px;
  display: flex;
  width: 30%;
  padding: 5px 0;
  margin: 0 5px;
  // opacity: 0;
  transition: opacity 0.5s ease-in-out;
  cursor: pointer;
  height: 83px;
  box-sizing: border-box;
}

.slide.active, .slide:hover, .slide:focus {
  border-bottom: 4px solid gray;
}

.slide img {
  width: 100%;
  // height: calc(100% - 4px);
}
.slider .desc {
  background-color: rgb(193 110 103 / 60%);
  height: 110px;
  box-sizing: border-box;
  color: white;
  font-size: 22px;
  font-family: "arial";
  padding: 10px;
  margin: 0;
  font-weight: bold;
  overflow: hidden;
  text-align: right;
}
.slider .desc:hover {
  color: #CCCCCC;
}
.enlarged-image {
  width: 100%;
  height: 70%;
}

.slides.auto-play {
  animation: auto-play 3s linear infinite;
}

@keyframes auto-play {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
