.EditorsUs {
  color: black;
  .title {
    height: 35px;
    padding: 0 15px;
    font-size: 19px;
    background-color: #eee;
    font-weight: bold;
    color: var(--sec-color);
    display: flex;
    align-items: center;
  }
  .subtitle {
    font-family: "arial";
    font-size: 1.7647em;
    font-weight: bold;
    color: var(--main-color);
    margin: 10px 0;
  }
  .aboutcontent {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-top: 30px;

    .item {
      font-weight: 600;
      font-size: 16px;

      &:nth-child(1) {
        font-size: 23px;
        margin-bottom: 15px;
      }
      &:nth-child(2) {
        font-size: 22px;
      }
      &:nth-child(3) {
        font-size: 20px;
      }
      &:nth-child(4) {
        font-size: 18px;
      }
      &:nth-child(5) {
        font-size: 16px;
      }
    }
  }
}
