.sections {
  flex: 2;
  padding: 10px;
  .title {
    padding: 20px 0 15px 0;
    color: var(--sec-color);
    font-weight: bold;
    font-size: 20px;
  }
  .oneNews {
    color: inherit;
    .oneTitle {
        font-weight: bold;
        color: #a6aaab;
        font-size: 18px;
    }
    .time {
      font-weight: bold;
      font-size: 14px;
      color: #333333;
      margin-top: 5px;
    }
    .oneContent {
      display: flex;
      gap: 7px;
      font-weight: bold;
      color: #555555;
      line-height: 22px;
      word-spacing: 1px;
      margin-top: 10px;

      border-bottom: 1px dotted #ECEDE8;
      border-bottom-style: dotted;
      margin-bottom: 10px;
      border-bottom-width: 1px;
      border-bottom-color: #CCCCCC;
      padding: 10px;
      img {
        height: 150px;
        width: 240px;
        border-width: 0px;
        border: 1px solid #000000;
      }
      .desc {
        color: inherit;
      }
    }
  }
}
