.tabs {
    padding: 10px;
    background-color: white;
    margin: 0 10px ;
    .tapwrapper {
    height: 240px;
    border: 1px solid #ccc;

    background: #eee none repeat scroll 0 0;
}
  .top {
    padding: 2px 0;
    border-bottom: 3px solid var(--sec-color);
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    span {
        color: #555;
        font-family: sans-serif;
        font-weight: bold;
        font-style: normal;
        font-size: 17px;
        line-height: 22px;
        text-align: right;
        padding: 3px 22px;
        cursor: pointer;
      &.active {
        color: white;
        background-color: #A6AAAB;
      }
    }
  }
  .items {
    display: flex;
    gap: 38px;
    padding: 0 7px;
    .item {
        display: flex;
        flex-direction: column;
        width: 136px;
        gap: 10px;
        img {
            
            width: 100%;
            height: 92px;
      }
      h2 {
        font-family: sans-serif, "arial";
    font-weight: bold;
    font-style: normal;
    color: #222221;
    font-size: 15px;
    line-height: 1.4;
      }
    }
  }
}
