.arts {
  flex: 2;
  padding: 10px;
  .title {
    padding: 20px 0 15px 0;
    color: var(--sec-color);
    font-weight: bold;
    font-size: 20px;
  }
  .oneNews {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: flex-start;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
    margin-bottom: 10px;
    .oneTitle {
        font-weight: bold;
        color: #a6aaab;
        font-size: 18px;
    }
    .time {
      font-weight: bold;
      font-size: 15px;
      color: #333333;
    }
    .left {
      display: flex;
      flex-direction: column;
      gap: 10px;
      .desc {
        color: var(--main-color);
        font-size: 17px;
        font-weight: bold;
      }
    }
    .oneContent {
      display: flex;
      gap: 7px;
      font-weight: bold;
      color: #555555;
      line-height: 22px;
      word-spacing: 1px;
      width: 162px;


      img {
        height: 130px;
        width: 120px;
        border-width: 0px;
        border: 1px solid #000000;
      }
    }
  }
}
