.news {
    height: 32px;
    position: fixed;
    bottom: 0;
    background: var(--main-color);
    width: 100%;
    display: flex;
    color: white;
    font-weight: 900;
    font-size: 17px;
    align-items: center;
    padding: 0 30px;
    gap: 20px;
    &>span {
        position: relative;
        color: white;
        &:hover {
            // text-decoration: underline;
        }
    }
    
}
.ityped-cursor {
    opacity: 1;
    -webkit-animation: blink 0.3s infinite;
    -moz-animation: blink 0.3s infinite;
    animation: blink 0.3s infinite;
    animation-direction: alternate;
    position: absolute;
    left: -3px;
    width: 3px;
    height: 81%;
    background-color: white;
    bottom: 1px;
}
 
@keyframes blink {
    100% {
        opacity: 0;
    }
}
 
@-webkit-keyframes blink {
    100% {
        opacity: 0;
    }
}
 
@-moz-keyframes blink {
    100% {
        opacity: 0;
    }
}