.LeftHomeOthers {
  color: black;
  h3 {
    color: var(--sec-color);
    padding: 10px 5px 5px;
    border-bottom: 2px solid #999;
  }
  .litems {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
    .litem {
      background: #ccc;
      span {
        padding: 10px;
        font-size: 17px;
        display: block;
        color: black;
      }
      img {
        max-width: 100%;
      }
    }
  }
  .more {
    width: fit-content;
    border: 1px solid #aaa;
    padding: 4px 8px;
    border-radius: 6px;
    margin-right: auto;
    margin-top: 15px;
    display: flex;
    gap: 5px;

    span {
      font-size: 18px;
      background: black;
      color: white;
      border-radius: 50%;
      width: 19px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 19px;
      font-weight: 900;
    }
  }
  .writes {
    margin-top: 5px;
    .onewrite {
      display: flex;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #c7c9ca;
    gap: 10px;
      img {
        height: 102px;
        width: 102px;
        padding: 3px;
        border: 1px solid #999;
      }
      .info {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 5px;
        font-size: 17px;
        .title {
          color: inherit;
        }
        .writer {
          color: var(--main-color);
          align-self: flex-end;
        }
      }
    }
  }
  .shares {
    margin-top: 5px;
    .onewrite {
      display: flex;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #c7c9ca;
    gap: 40px;
      img {
        height: 62px;
        width: 62px;
      }
      .info {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 5px;
        font-size: 17px;
        .title {
          color: inherit;
        }
        .writer {
          color: #c7c9ca;
          align-self: flex-end;
        }
      }
    }
  }
  .ads {
    margin-top: 10px;
     h3 {
      margin-bottom: 10px;
    color: #A6AAAB;
    border-bottom: 3px solid #4f4f4e;
     }
  }
}
